<template>
    <div>
        <div class="banner">
            <div class="banner-wrap clearfix">
                <h2 class="banner-tit">{{compData.company_name}}</h2>
            </div>
        </div>
        <!--comp-detail-->
        <div class="comp-detail">
            <div class="wrap">
                <div class="comp-detail-left fl">
                    <div class="detail-headimg">
                        <img src="../assets/img/pic_qiye01.png" alt="headimg">
                    </div>
                    <div class="detail-text-contain">
                        <h3 class="detail-tit">企业介绍：</h3>
                        <div class="detail-text" v-html="compData.introduce"></div>
                    </div>
                    <div class="detail-video">
                        <h3 class="detail-tit">宣传短片：</h3>
                        <div class="detail-video-contain">
                            <iframe :src="compData.company_video" frameborder="0"
                                    allowfullscreen>
                            </iframe>
                        </div>
                    </div>
                    <div class="detail-evalu">
                        <div class="evalu-top">
                            <h3 class="detail-tit evalu-tit">用户评价：</h3>
                            <a class="evalu-btn-jump btn pink" href="#evalu-tarea">我要评价</a>
                        </div>
                        <div class="evalu-middle">
                            <div class="evalu-item" v-for="item in evaluData">
                                <div class="evalu-content evalu-time">{{item.create_time}}</div>
                                <div class="evalu-content evalu-text">{{item.create_time}}</div>
                                <div class="evalu-content evalu-username">{{item.user_name}}</div>
                            </div>
                        </div>
                        <div class="evalu-bottom">
                        <textarea id="evalu-tarea"
                                  class="evalu-tarea"
                                  placeholder="请输入评价内容"
                                  v-model="evaluVal"></textarea>
                            <span class="word-tip fr"><span class="word-num">0</span>字 / 还可以输入<span
                                    class="remain-num">100</span>字</span>
                            <button class="evalu-btn-submit btn gold fr" @click="submitEvalu">提交</button>
                        </div>
                    </div>
                </div>
                <div class="comp-detail-right fl">
                    <div class="comp-detail-contact">
                        <!--联系人字段-->
                        <h3 class="detail-contact-top">{{compData.name}}</h3>
                        <div class="detail-contact-bottom">
                            <div class="detail-contact-content">
                                <span class="detail-contact-tit">联系方式：</span>
                                <span class="detail-contact-text">{{showTel ? compData.tel : hiddenTel}}</span>
                            </div>
                            <div class="detail-contact-content">
                                <span class="detail-contact-tit">业务范围：</span>
                                <span class="detail-contact-text contact-busin">{{compData.category}}</span>
                            </div>
                            <div class="detail-contact-btns">
                                <button class="detail-contact-btn btn pink fl" @click="lookFullTel">查看完整号码</button>
                                <button class="detail-contact-btn btn green fr" @click="collectComp">加入收藏</button>
                                <button class="detail-contact-btn btn gold" @click="signContract">签订合同</button>
                                <a target="_blank"
                                   :href="`http://wpa.qq.com/msgrd?v=3&uin=${compData.qq}&site=qq&menu=yes`">
                                    <i class="detail-contact-qq icon icon-qq"></i>
                                </a>
                            </div>
                        </div>
                    </div>
                    <div class="detail-out-btns">
                        <a href="/pc/views/index.html">
                            <button class="detail-out-btn yellow">返回首页</button>
                        </a>
                        <a href="javascript:history.go(-1);">
                            <button class="detail-out-btn black">返回上一页</button>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import api from '../state/api';
    import bus from '../state/bus';
    import func from '../func/func';
    let queryByName = func.getQueryStringByName;

    export default {
        name: 'comp-detail',
        data () {
            return {
                compData: {
                    user_name: '',
                    company_name: '杭州网易',
                    company_address: '',
                    name: '李先生',
                    tel: '18888888888',
                    qq: '',
                    introduce: '',
                    category: '', // 业务范围
                    company_banner: [], // 头图
                    company_video: '', // 视频
                    authentication: '', // 认证
                },
                evaluData: [
                    // {
                    //     create_time: '',
                    //     user_name: '',
                    //     evaluate_content: '',
                    // }
                ],
                comp_id: '', // 公司id
                showTel: false,  // 完整号码flag
                evaluVal: '',  // v-model评价输入框
            };
        },
        computed: {
            // 将号码隐藏
            hiddenTel () {
                return this.compData.tel.replace(/(\d{3})(\S+)/, '$1********');
            },
            user_id  () {
                return this.$store.state.user_id;
            },
        },
        methods: {
            // 获取公司详情数据
            getCompData () {
                this.$http.get(api.comp_detail,
                    {
                        company_id: this.comp_id,
                    }
                )
                    .then(res => {
                        this.compData = res.data;
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            // 获取评论数据
            getEvaluData () {
                this.$http.get(api.comp_evalu,
                    {
                        company_id: this.comp_id,
                    }
                )
                    .then(res => {
                        this.evaluData = res.data.data;
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            // 发送评论
            submitEvalu () {
                this.$http.post(api.ubmit_evalu,
                    {
                        company_id: this.comp_id,
                        user_id: this.user_id,
                        content: '',
                    }
                )
                    .then(res => {
                        switch (res.data.status) {
                            case 200:
                                this.$message.success('评价成功');
                                break;
                            case 100:
                                this.$message.warning('评价次数不足');
                                break;
                        }
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            // 查看完整号码
            lookFullTel () {
                if (this.user_id) {
                    this.showTel = true;

                    // 查看号码后加入我的客户
                    this.$http.post(api.custom_option, {
                        user_id: this.user_id,
                        company_id: this.comp_id,
                        btn_value: 0,
                    })
                        .then()
                        .catch(err => {
                            console.log(err);
                        });

                } else {
                    this.$message.warning('请登录后继续操作');
                }
            },
            // 加入收藏
            collectComp () {
                this.$http.post(api.add_collect, {
                        id: this.comp_id,
                        user_id: this.user_id,
                    }
                )
                    .then(res => {
                        switch (res.data.status) {
                            case 200:
                                this.$message.success('收藏成功');
                                break;
                            case 300:
                                this.$message.warning('已收藏');
                                break;
                            default:
                                this.$message.error('收藏失败');
                                break;
                        }
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            // 签订合同
            signContract () {
            },
        },
        beforeCreate () {
            this.$loading(); //loading动画
            setTimeout(() => {
                this.$loading().close(); //关闭加载动画
            }, 800);
        },
        created () {
            // 从query获取comp_id 如果不存在跳转首页
            this.comp_id = queryByName('comp_id');
            if (!this.comp_id || this.comp_id === 'undefined') {
                window.location.href = '/pc/views/index.html';
            } else {
                this.getCompData();
                this.getEvaluData();
            }
        },
    };
</script>


