Vue.use(VueResource);
Vue.use(addressPicker);
Vue.use(ElementUI);
Vue.component('my-page', Pagination);
Vue.http.options.emulateJSON = true;
Vue.http.options.xhr = {withCredentials: true};
Vue.http.options.emulateHTTP = true;
Vue.http.options.crossOrigin = true;


