<template>
    <!--引入微信登录js-->
    <div class="header">
        <div class="header-top">
            <div class="header-wrap clearfix">
                <div class="header-city fl">
                    <i class="icon icon-city"></i>{{addressShow.province}} · {{addressShow.city}}


                </div>
                <div class="address fl" v-if="inIndex">
                    <button class="header-city-btn"
                            @click="selectCity">{{addressFlag ? '[确定]' : '[切换城市]'}}


                    </button>
                    <address-picker
                            v-if="addressFlag"
                            :opts="addressPick"
                            v-model="addressValue">
                    </address-picker>
                </div>
                <ul class="header-links header-links-top fr">
                    <li>
                        <button class="header-a" @click="signIn" v-if="!isSign">登录</button>
                        <span class="header-a" v-else>欢迎您，{{user_name}}</span>
                    </li>
                    <li>
                        <button class="header-a" @click="signUp" v-if="!isSign">注册</button>
                        <button class="header-a" @click="signOut" v-else>注销</button>
                    </li>
                    <li><button class="header-a" @click="toUserCenter">个人中心</button></li>
                    <li class="header-qr-contain">
                        <button href="" class="header-a header-show-qr">关注我们</button>
                        <img src="../assets/img/pic_erweima_header.png" alt="关注我们" class="header-qr">
                    </li>
                </ul>
            </div>
        </div>
        <div class="header-middle">
            <div class="header-wrap clearfix">
                <div class="header-logo fl">
                    <img src="../assets/img/icons/logo.png" alt="logo">
                </div>
                <div class="header-search fr">
                    <input placeholder="请输入搜索关键词"
                           autocomplete="off"
                           icon="search"
                           v-model="searchKeyword">
                    <div class="btn-search" @click="handleSearch"></div>
                </div>
            </div>
        </div>
        <div class="header-bottom">
            <div class="header-wrap clearfix">
                <div class="nav fl"
                     @mouseenter="navMouseIn"
                     @mouseleave="navMouseOut">
                    <div class="nav-show"><i class="icon icon-nav"></i>工程企业</div>
                    <transition name="custom-classes-transition"
                                enter-active-class="animated fadeIn"
                                leave-active-class="animated fadeOut">
                        <!--<transition name="slide">-->
                        <div class="nav-hidden" v-show="inIndex?true:navFlag">
                            <ul>
                                <li class="nav-item"
                                    v-for="(li, liIndex) in threeClassify"
                                    :class="{active: liFlag[liIndex]}"
                                    @mouseenter="tabNavContent(liIndex)">{{li.label}}


                                </li>
                            </ul>
                            <transition name="custom-classes-transition"
                                        enter-active-class="animated fadeIn nav-ani"
                                        leave-active-class="animated fadeOut nav-ani">
                                <div class="nav-content"
                                     v-show="navFlag">
                                    <h3 class="nav-content-tit">{{threeClassify[contentIndex].label}}</h3>
                                    <div class="nav-content-links">
                                        <!--链接以query方式传参-->
                                        <a target="_blank" class="nav-content-a"
                                           v-for="a in threeClassify[contentIndex].children"
                                           :href="`/pc/views/comp-list.html?area=${areaValue}` +
                                           `&fid=${threeClassify[contentIndex].value}&sid=${a.value}`">{{a.label}}</a>
                                    </div>
                                    <img class="nav-content-img" :src="threeClassify[contentIndex].pic">
                                </div>
                            </transition>
                        </div>
                    </transition>
                </div>
                <ul class="header-links header-links-bottom fl">
                    <li><a href="/pc/views/index.html" class="header-a">首页</a></li>
                    <li><button class="header-a" @click="toUserCenter">个人中心</button></li>
                </ul>
                <div class="login-info"
                     v-if="inIndex">
                    <div class="user-info">
                        <div class="user-avatar">
                            <img src="../assets/img/pic_avatar.png" alt="avatar">
                        </div>
                        <span class="user-name" v-if="!isSign">未登录，登录后显示用户名</span>
                        <div v-else>
                            <span class="user-name">账号：{{user_name}}</span>
                            <span class="user-name">企业名称：{{company_name}}</span>
                        </div>
                    </div>
                    <div class="login-btns">
                        <button class="sign-in fl" @click="signIn" v-if="!isSign">登录</button>
                        <a href="/pc/views/user-center.html" v-else>
                            <button class="sign-in fl">个人中心</button>
                        </a>
                        <button class="sign-up fr" @click="signUp" v-if="!isSign">注册</button>
                        <button class="sign-up fr" @click="signOut" v-else>退出</button>
                    </div>
                    <div class="comp-count">
                        <span class="comp-count-tit">入驻企业(家)</span>
                        <span class="comp-count-num">{{totalCompany}}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import bus from '../state/bus';
    import api from '../state/api';
    import func from  '../func/func';

    export default {
        name: 'Header',
        data() {
            return {
                searchKeyword: '',
                inIndex: false, // 是否在首页
                navFlag: false,
                liFlag: [true], // active li
                addressFlag: false,
                addressShow: {province: '', city: '', district: ''}, // 显示的城市
                addressValue: {province: '', city: '', district: ''}, // address-picker v-model
                addressPick: {
                    'default': {province: '请选择', city: '请选择', district: '请选择'},
                    'noLabel': true,
                },
                contentIndex: 0, // 内容index
                threeClassify: [
                    {
                        value: '',
                        label: '',
                        pic: '',
                        children: [],
                    }
                ],
                totalCompany: 0,
                isSign: false, // 是否登录
                user_name: '', // 用户名
                company_name: '未填写', // 企业名称
                user_id: '',
            }
        },
        computed: {
            // 拼接地区选择字符串 并url-encode
            areaValue () {
                let adr = this.addressShow;
                adr = adr.province + adr.city + adr.district;
                return adr;
            },
        },
        watch: {
            areaValue () {
                this.$store.commit('area', this.areaValue);
            }
        },
        methods: {
            navMouseIn () {
                this.navFlag = true;
            },
            navMouseOut () {
                this.navFlag = false;
            },
            tabNavContent (liIndex) {
                this.contentIndex = liIndex;
                let li = this.liFlag;
                for (let i = 0; i < this.threeClassify.length; i++) {
                    li[i] = false;
                }
                li[liIndex] = true;
            },
            // 呼出登录窗口
            signIn () {
                bus.$emit('login', {loginShow: true, loginType: 0});
            },
            signUp () {
                bus.$emit('login', {loginShow: true, loginType: 1});
            },
            // 注销
            signOut () {
                this.$http.get(api.sign_out)
                    .then(res => {
                        switch (parseInt(res.data.status)) {
                            case 200:
                                this.$message.success('注销成功');
                                this.isSign = false;
                                break;
                            default:
                                this.$message.error('系统故障');
                                break;
                        }
                    })
                    .catch(err => {
                        console.log(err);
                    })
            },
            handleSearch () {
                let val = this.searchKeyword && this.searchKeyword.trim();
                if (!val) {
                    this.$message.warning('请输入搜索关键词');
                } else {
                    location.href = `/pc/views/search-list.html?keyword=${val}`
                }
            },
            selectCity () {
                if (this.addressFlag) {
                    // 切换城市ajax
                    this.addressShow = this.addressValue;
                }
                this.addressFlag = !this.addressFlag;
            },
            // cookie验证登录
            cookieSignIn () {
                this.$http.get(api.check_login)
                    .then(res => {
                        switch (res.data.status) {
                            case 200:
                                this.isSign = true;
                                this.user_name = res.data.user_name;
                                this.company_name = res.data.company_name;
                                this.user_id = res.data.user_id;
                                this.$store.commit('user_id', res.data.user_id);
                                break;
                            default:
//                                this.isSign = false; // todo 调试时注释
//                                // 非法进入个人中心 跳回首页
//                                if (window.location.href.match(/pc\/views\/user/)) {
//                                    window.location.href='/pc/views/index.html?login=1';
//                                }
                                break;
                        }
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            // 个人中心链接 防止未登录跑进个人中心
            toUserCenter () {
              if (this.user_id) {
                  window.location.href = '/pc/views/user-center.html';
              } else {
                  bus.$emit('login', {loginShow: true, loginType: 0});
                  this.$message.warning('请登陆后继续操作');
              }
            },
        },
        created () {

            this.cookieSignIn();

            bus.$on('signIn', () => {
                this.cookieSignIn();
            });

            // 根据ip获取地址
            this.$http.get(api.get_dist_from_ip)
                .then(res => {
                    let data = res.data.content.address_detail;
                    let adr = this.addressShow;
                    adr.province = data.province;
                    adr.city = data.city;
                })
                .catch(err => {
                    console.log(err);
                });

            // 获取导航分类
            this.$http.get(api.nav_classify)
                .then(res => {
                    this.totalCompany = res.data.count;

                    let arr = res.data.data || [];
                    this.threeClassify = func.createClassify(arr);
                })
                .catch(err => {
                    console.log(err);
                });

        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .header {
        .header-top {
            height: 50px;
            line-height: 50px;
            background: #fff;
            color: #736037;
        }

        .header-middle {
            background: #0F0F0F;
            height: 90px;
            line-height: 90px;
        }

        .header-bottom {
            background: #fff;
            height: 41px;
            line-height: 39px;
            border-bottom: 1px #CBAC86 solid;
        }

        .header-wrap {
            width: 1200px;
            margin: 0 auto;
            position: relative;
            clear: both;
        }

        .header-top,
        .header-middle {
            .header-wrap {
                padding: 0 30px;
            }
        }

        .header-city {
            height: 100%;
            .header-city-btn {
                text-align: center;
                width: 70px;
            }
        }

        .address {
            overflow: hidden;
            > button {
                margin-left: 10px;
                margin-right: 10px;
            }
            float: left;
            .address-picker {
                float: right;
                margin-top: 2px;
                display: inline-block;
                height: 48px;
            }
        }

        .header-links {
            height: 100%;
            > li {
                float: left;
            }
            .header-a {
                display: inline-block;
                height: 100%;
            }
        }

        .header-links-top {
            .header-a {
                font-size: 14px;
                text-align: center;
                padding: 0 30px;
                color: #736037;
                height: 50px;
            }
            // 二维码
            .header-qr-contain {
                position: relative;
            }
            .header-qr {
                position: absolute;
                right: -30px;
                top: 46px;
                display: none;
                opacity: 0;
                z-index: 1000;
                transition: all .2s ease;
                box-shadow: 0 1px 4px 1px #999;
            }

            .header-show-qr:hover + .header-qr {
                display: block;
                opacity: 1;
            }
        }

        .header-links-bottom {
            .header-a {
                font-size: 18px;
                width: 120px;
                text-align: center;
                height: 40px;
                color: #333;
                &.active {
                    color: #CBAC86;
                }
            }

        }

        .header-logo {
            height: 100%;
            > img {
                vertical-align: middle;
            }
        }

        .header-search {
            position: relative;
            > input {
                width: 246px;
                height: 35px;
                padding: 0 10px;
                color: #DEC69D;
                background: #0F0F0F url(../assets/img/icons/seach_input.png) no-repeat center;
            }
            input:-ms-input-placeholder {
                color: #DEC69D;
            }
            input::-moz-placeholder {
                color: #DEC69D;
            }
            input::-webkit-input-placeholder {
                color: #DEC69D;
            }
            .btn-search {
                position: absolute;
                width: 50px;
                height: 100%;
                right: 0;
                top: 0;
                cursor: pointer;
            }
        }

        .nav {
            width: 210px;
            height: 41px;
            text-align: center;
            line-height: 41px;
            position: relative;
            font-size: 14px;
            .nav-show {
                width: 100%;
                height: 100%;
                background: #CBAC86;
                color: #fff;
                font-size: 20px;
            }
            .nav-hidden {
                position: absolute;
                left: 0;
                top: 41px;
                width: 100%;
                background: #212121;
                text-align: left;
                z-index: 10;
                .nav-item {
                    width: 100%;
                    height: 44px;
                    color: #FFF;
                    background: #212121;
                    border-bottom: 0.5px #000 solid;
                    border-top: 0.5px #666 solid;
                    padding-left: 37px;
                    &.active,
                    &:hover {
                        background: #fff;
                        border: 1px solid #CBAC86;
                        color: #CBAC86;
                    }
                }
                .nav-content {
                    width: 730px;
                    height: 484px;
                    position: absolute;
                    left: 210px;
                    top: 0;
                    padding: 15px 60px;
                    background: #fff;
                    box-shadow: 1px 1px 4px 1px #999;
                    .nav-content-tit {
                        font-size: 24px;
                        height: 24px;
                        line-height: 24px;
                        padding-left: 7px;
                        margin-bottom: 27px;
                        color: #CBAC86;
                        border-left: 4px solid #CBAC86;
                    }
                    .nav-content-a {
                        margin-right: 29px;
                        font-size: 14px;
                        color: #999;
                    }
                    .nav-content-img {
                        width: 610px;
                        height: 300px;
                        position: absolute;
                        bottom: 34px;
                    }
                }
            }

        }

        .login-info {
            width: 261px;
            height: 484px;
            background: #fff;
            box-shadow: 0 1px 4px 1px #999;
            position: absolute;
            right: 0;;
            top: 40px;
            z-index: 11;
            text-align: center;
            .user-info {
                padding-top: 35px;
                height: 200px;
                .user-avatar {
                    border-radius: 100px;
                    width: 61px;
                    height: 61px;
                    display: block;
                    margin: 0 auto;
                    margin-bottom: 10px;
                }
                .user-name {
                    display: block;
                    margin: 0 auto;
                }
            }

            .login-btns {
                overflow: hidden;
                border-top: 1px solid #EBEBEB;
                border-bottom: 1px solid #EBEBEB;
                font-size: 16px;
                .sign-in, .sign-up {
                    width: 50%;
                    height: 40px;
                    background: #fff;
                    color: #666;
                    border-right: 1px solid #EBEBEB;
                    &:before {
                        content: '';
                        display: inline-block;
                        vertical-align: text-top;
                        margin-right: 5px;
                    }
                    &:hover {
                        background: #CBAC86;
                        color: #fff;
                    }
                }
                .sign-in:before {
                    width: 15px;
                    height: 14px;
                    background: url(../assets/img/icons/icon_denglu_weixuanze.png) no-repeat center;
                }
                .sign-up:before {
                    width: 12px;
                    height: 14px;
                    background: url(../assets/img/icons/icon_zhuce_weixuanze.png) no-repeat center;
                }
                .sign-in:hover:before {
                    background-image: url(../assets/img/icons/icon_denglu_xuanze.png);
                }
                .sign-up:hover:before {
                    background-image: url(../assets/img/icons/icon_zhuce_xuanze.png);
                }
            }

            .comp-count {
                padding-top: 70px;
                .comp-count-tit {
                    display: block;
                    margin: 0 auto;
                    margin-bottom: 12px;
                    font-size: 20px;
                    color: #333;
                }
                .comp-count-num {
                    color: #CBAC86;
                    font-size: 40px;
                }
            }
        }

        .icon-city {
            width: 20px;
            height: 20px;
            margin-right: 4px;
            background: url(../assets/img/icons/icon_daa.png) no-repeat center;
        }

        .icon-nav {
            width: 17px;
            height: 14px;
            margin-right: 4px;
            vertical-align: baseline;
            background: url(../assets/img/icons/icon_xuanze01.png) no-repeat center;
        }
        .nav-ani {
            animation-duration: 0.3s;
        }
    }
</style>


