<template>
    <transition name="custom-classes-transition"
                enter-active-class="animated fadeIn login"
                leave-active-class="animated fadeOut">
        <div class="login-popup" v-show="loginShow">
            <div class="login-dialog animated fadeInDown" v-show="loginShow && !wxShow">
                <button class="login-btn-close" @click="closeLogin"></button>
                <img class="login-dialog-left" src="../assets/img/pic_login.png" alt="登录">
                <div class="login-dialog-right animated"
                     :class="{bounceIn: isChanging}">
                    <h4 class="login-dialog-tit">{{loginText[loginType].tit}}</h4>
                    <span class="login-dialog-span">{{loginText[loginType].span1}}</span>
                    <input class="login-dialog-input"
                           autocomplete="off"
                           v-if="inputType1"
                           type="password"
                           v-model="val1">
                    <input class="login-dialog-input"
                           autocomplete="off"
                           v-else
                           type="text"
                           v-model="val1">
                    <span class="login-dialog-span">{{loginText[loginType].span2}}</span>
                    <input class="login-dialog-input"
                           autocomplete="off"
                           v-if="inputType2"
                           type="password"
                           v-model="val2"
                           @keyup.enter="handleEnter">
                    <input class="login-dialog-input"
                           autocomplete="off"
                           v-else
                           type="text"
                           v-model="val2"
                           @keyup.enter="handleEnter">
                    <div class="login-btns-type-0"
                         v-if="loginType === 0">
                        <div class="login-btns-type-0-top">
                            <button class="login-forget" @click="jumpForget">忘记密码</button>
                            <button class="login-sign-up" @click="jumpSignUp">没有账号注册一个 >></button>
                        </div>
                        <button class="login-sign-in" @click="handleSignIn">登录</button>
                        <span class="login-or">or</span>
                        <button class="login-sign-wx"
                                @click="weixinSign"></button>
                    </div>
                    <div class="login-btns-type-1"
                         v-if="loginType === 1 || loginType === 3">
                        <button class="login-btn-main" @click="doneVerify">{{loginText[loginType].btn}}</button>
                        <button class="login-verify-code" :class="{gray: countdown}" @click="sendVerifyCode">
                            {{countdown ? `${countdown}秒后重新发送` : '点击发送验证码'}}

                        </button>
                    </div>
                    <div class="login-btns-type-2"
                         v-if="loginType === 2 || loginType === 4">
                        <button class="login-btn-main" @click="setPass">确认</button>
                    </div>
                    <!--<span class="login-tips">{{loginTips}}</span>-->
                </div>
            </div>
            <div class="wx-dialog animated fadeInDown" v-show="wxShow">
                <button class="login-btn-close" @click="closeLogin"></button>
                <h4 class="wx-dialog-tit"><i class="login-sign-wx"></i>微信登录</h4>
                <div id="wx-qrcode"></div>
                <!--<img src="../assets/img/pic-qrcode.png" alt="" class="wx-dialog-qrcode">-->
                <!--<span class="wx-dialog-span">请使用微信的扫一扫以登录</span>-->
            </div>
        </div>
    </transition>
</template>

<script>
    import bus from '../state/bus';
    import api from '../state/api';
    import func from '../func/func';
    let queryByName = func.getQueryStringByName;

    export default {
        name: 'login',
        data() {
            return {
                loginShow: false,
                wxShow: false,
                isChanging: false, // 切换type动画
                countdown: 0, // 验证码倒计时
                val1: '',
                val2: '',
                // 5种状态 从0-4 分别对应登录、注册、设置密码、找回密码、重设密码
                loginType: 0,
                loginText: [
                    {
                        tit: '欢迎登录',
                        span1: '手机号码',
                        span2: '密码',
                    },
                    {
                        tit: '欢迎注册',
                        span1: '手机号码',
                        span2: '验证码',
                        btn: '确认注册',
                    },
                    {
                        tit: '设置密码',
                        span1: '密码',
                        span2: '再次输入密码',
                    },
                    {
                        tit: '安全验证',
                        span1: '注册手机号',
                        span2: '验证码',
                        btn: '验证',
                    },
                    {
                        tit: '设置密码',
                        span1: '新密码',
                        span2: '再次输入密码',
                    },
                ],
                tel: '', // 手机号码
                isSign: false, // 是否登录
            }
        },
        computed: {
            inputType1() {
                let result;
                const type = this.loginType;
                if (type === 2 || type === 4) {
                    // password
                    result = 1;
                } else {
                    // text
                    result = 0;
                }
                return result;
            },
            inputType2() {
                let result;
                const type = this.loginType;
                if (type === 1 || type === 3) {
                    // text
                    result = 0;
                } else {
                    // password
                    result = 1;
                }
                return result;
            },
        },
        methods: {
            // 关闭login
            closeLogin () {
                this.loginShow = false;
                this.wxShow = false;
                this.clearVal();
            },
            // 清空输入框 跳转时需清空
            clearVal () {
                this.val1 = '';
                this.val2 = '';
            },
            // 校验手机号格式
            verifyPhone (val) {
                let regPhone = /(086-?)?(13|14|15|18)\d{9}/;
                if (!val.match(regPhone)) {
                    this.$message.warning('请填写正确的手机号码');
                    return 1;
                } else {
                    return 0;
                }
            },
            // 校验密码格式
            verifyPass (val) {
                let regPass = /([a-zA-Z0-9]|[._]){5,20}$/;
                if (!val.match(regPass)) {
                    this.$message.warning('请设置5-20位密码');
                    return 1;
                } else {
                    return 0;
                }
            },
            // 点击微信登录
            weixinSign () {
                this.wxShow = true;
            },
            // 点击登录验证
            handleSignIn () {
                if (!this.val1 || !this.val2) {
                    this.$message.warning('请填写完整的登录手机号和密码');
                    return;
                }
                // 提示在函数里
                if (this.verifyPhone(this.val1)) {
                    return;
                }
                if (this.verifyPass(this.val2)) {
                    return;
                }
                this.$http.post(api.sign_in, {
                    tel: this.val1,
                    password: this.val2
                })
                    .then(res => {
                        switch (parseInt(res.data.status)) {
                            case 100:
                                this.$message.warning('未注册，请先完成注册');
                                this.loginType = 1; // 跳注册
                                break;
                            case 200:
                                this.$message.success('登录成功，欢迎你');
                                this.loginShow = false;
                                this.clearVal();
                                bus.$emit('signIn'); // 通知header获取用户信息
                                break;
                            default:
                                this.$message.error('用户名或密码错误');
                                break;
                        }
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            // 点击忘记密码btn跳找回密码
            jumpForget () {
                this.clearVal();
                this.loginType = 3;
            },
            // 点击注册一个btn跳注册
            jumpSignUp () {
                this.clearVal();
                this.loginType = 1;
            },
            // 点击发送验证码按钮
            sendVerifyCode () {
                if (this.verifyPhone(this.val1) || this.countdown) {
                    return;
                }
                this.$http.post(api.get_verify_code, {tel: this.val1}).then(res => {
                    console.log(res);
                    switch (res.data.data) {
                        case 100:
                            this.$message.error('验证码发送失败，请稍后再试');
                            break;
                        case 200:
                            this.$message.success('验证码发送成功，请在十五分钟内完成验证');
                            this.countdown = 60;
                            let timer = setInterval(() => {
                                this.countdown--;
                                if (this.countdown === 0) {
                                    clearInterval(timer);
                                }
                            }, 1000);
                            break;
                        default:
                            break;
                    }
                }).catch(err => {
                    console.log(err);
                });
            },
            // 验证成功跳设密码 失败提示
            doneVerify () {
                this.$http.post(api.check_verify_code,
                    {code: this.val2}
                ).then(res => {
                    switch (parseInt(res.data)) {
                        //                        case '100':
                        //                            this.$message.error('验证码错误');
                        //                            break;
                        case 200:
                            this.$message.success('验证成功，请设置密码');
                            this.tel = this.val1;
                            this.clearVal();
                            if (this.loginType === 1) { // 注册
                                this.loginType = 2;
                            } else { // 重设密码
                                this.loginType = 4;
                            }
                            break;
                        default:
                            this.$message.error('验证码错误');
                            break;
                    }
                }).catch(err => {
                    console.log(err);
                });
            },
            // 设置密码 设置成功后直接登陆
            setPass () {
                if (this.val1 && this.val1 === this.val2) {
                    this.$http.post(api.set_password, {mobile: this.tel, pwd: this.val2}).then(res => {
                        switch (parseInt(res.data.result)) {
                            case 200:
                                this.$message.success('登录成功，欢迎你');
                                this.loginShow = false;
                                bus.$emit('signIn'); // 通知header获取用户信息
                                this.clearVal();
                                break;
//                            case 100:
//                                this.$message.warning('新密码不能与旧密码相同');
//                                break;
//                            default:
//                                break;
                        }
                    }).catch(err => {
                        console.log(err);
                    });
                } else {
                    this.$message.error('两次输入的密码不一致, 请重试');
                }
            },
            // 第二个输入框回车事件
            handleEnter () {
                switch (this.loginType) {
                    case 0:
                        // 登录
                        this.handleSignIn();
                        break;
                    case 1:
                    case 3:
                        // 注册和找回都是对比验证码
                        this.doneVerify();
                        break;
                    case 2:
                    case 4:
                        // 设置密码
                        this.setPass();
                        break;
                }
            }
        },
        created() {
            // 微信第一次登陆 绑定手机号
            if (queryByName('first')) {
                this.loginShow = true;
                this.loginType = 1;
                this.$message.success('新用户请绑定手机号');
            }

            // 未登录进个人中心
            // 如果跳转url query中login=0则呼出登录窗口
            if (queryByName('login')) {
                this.loginShow = true;
                this.loginType = 0;
                this.$message.warning('请登陆后继续操作');
            }

            // 注意this绑定 非箭头函数this指向bus需使用bind(this) hack;
            bus.$on('login', arg => {
                this.loginShow = arg.loginShow;
                this.loginType = arg.loginType;
            });
        },
        mounted () {
            let wx = new WxLogin({
                id: "wx-qrcode",
                appid: "wxc909bc6835c99bfb",
                scope: "snsapi_login",
                redirect_uri: "http%3A%2F%2Fcivil.gyl.sunday.so%2Findex%2Fregister%2Fweb_access",
            });
        },
        watch: {
            loginType() {
                this.isChanging = true;
                setTimeout(() => {
                    this.isChanging = false;
                }, 800);
            },
        },
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .login.animated {
        animation-duration: 0.5s;
    }

    .login-popup {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.4);
        z-index: 1000;
        .animated {
            animation-duration: 0.5s;
        }
        .login-btn-close {
            background: url(../assets/img/icons/icon_close.png) no-repeat center;
            width: 64px;
            height: 64px;
            position: absolute;
            right: -32px;
            top: -32px;
            z-index: 10;
        }
        .login-sign-wx {
            display: inline-block;
            width: 64px;
            height: 64px;
            vertical-align: middle;
            background: url(../assets/img/icons/btn_weixin.png) no-repeat center;
        }
        .login-dialog {
            width: 850px;
            height: 500px;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -425px;
            margin-top: -250px;
            // transform: translate(-50%, -50%);
            border-radius: 10px;
            background: #fff;

            .login-dialog-left {
                float: left;
                width: 480px;
                height: 100%;
            }
            .login-dialog-right {
                float: right;
                width: 370px;
                height: 100%;
                padding: 67px 40px;
                position: relative;
                animation-duration: .8s;
            }
            .login-dialog-tit {
                text-align: center;
                font-size: 30px;
                color: #333;
                margin-bottom: 60px;
                font-weight: 700;
            }
            .login-dialog-span {
                display: block;
                font-size: 16px;
                color: #999;
            }
            .login-dialog-input {
                display: block;
                font-size: 16px;
                color: #666;
                width: 100%;
                border-bottom: 1px solid #666;
                padding: 8px 0;
                margin-bottom: 5px;
            }
            .login-btns-type-0 {
                .login-btns-type-0-top {
                    margin-top: 30px;
                    overflow: hidden;
                }
                .login-forget {
                    font-size: 14px;
                    color: #333;
                    float: left;
                    margin-bottom: 30px;
                }
                .login-sign-up {
                    font-size: 14px;
                    color: #CBAC86;
                    float: right;
                }
                .login-sign-in {
                    width: 200px;
                    height: 50px;
                    background: #333;
                    color: #fff;
                    font-size: 22px;
                    text-align: center;
                }
                .login-or {
                    /*margin: 0 1px;*/
                }
                .login-sign-wx {
                    margin-top: 5px;
                }
            }
            .login-verify-code {
                background: #FE6E8C;
                width: 120px;
                height: 30px;
                color: #fff;
                border-radius: 14px;
                position: absolute;
                right: 40px;
                top: 175px;
                &.gray {
                    background: #999;
                    cursor: default;
                }
            }
            .login-btn-main {
                background: #52BE71;
                width: 100%;
                height: 50px;
                font-size: 22px;
                color: #fff;
                margin-top: 60px;
            }
            .login-tips {
                display: inline-block;
                width: 100%;
                font-size: 16px;
                color: #ff4949;
                text-align: center;
                margin-top: 10px;
            }
            .login-btns-type-2 {
                .login-btn-main {
                    background: #000;
                }
            }
        }
        .wx-dialog {
            width: 400px;
            height: 550px;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -183px;
            margin-top: -255px;
            border-radius: 10px;
            background: #fff;
            text-align: center;
            .wx-dialog-tit {
                font-size: 24px;
                color: #333;
                font-weight: 700;
                margin-top: 50px;
                .login-sign-wx {
                    margin-right: 10px;
                }
            }
            .wx-dialog-qrcode {
                display: block;
                width: 210px;
                height: 210px;
                margin: 0 auto;
            }
            .wx-dialog-span {
                display: block;
                font-size: 16px;
                color: #999;
                margin-top: 10px;
            }
        }
    }
</style>


