import Vue from 'vue';
import '../assets/css/style.scss';
import '../main';
import CompDetail from '../renders/comp-detail.vue';
import store from '../state/store';

// 评论框字数限制及提示
const $tarea = $('.evalu-tarea');
$tarea.on('input', () => {
    let wordNum = $tarea.val().length;
    if (wordNum > 100) {
        $tarea.val($tarea.val().substring(0, 100));
        wordNum = 100;
    }
    $('.word-num').text(wordNum);
    $('.remain-num').text(100 - wordNum);
});

let app = new Vue({
    el: '#app',
    store,
    render: h => h(CompDetail),
});


