<template>
    <div class="page-wrap">
        <span class="blank" v-if="total === 0">{{message}}</span>
        <el-pagination
                v-if="total > 10"
                @current-change="handleCurrentChange"
                :page-size="10"
                :total="total"
                id="el-pagination"
                :layout="layout">
        </el-pagination>
    </div>
</template>

<script>
    export default {
        name: 'pagination',
        props: ['total', 'message', 'small'],
//        data () {
//          return {
//              curpage: 1,
//          }
//        },
        computed: {
            layout () {
                let layout;
                if (this.small) {
                    layout = 'prev, pager, next';
                } else {
                    layout = 'prev, pager, next, total, jumper';
                }
                return layout;
            }
        },
        methods: {
            handleCurrentChange(page) {
                this.$emit('clickpage', page);
            },
        },
    }
</script>

<style lang="scss">
    .user-right {
        position: relative;
    }

    .page-wrap {
        text-align: center;
    }

    .blank {
        display: block;
        font-size: 20px;
        margin: 30px auto;
    }

    #el-pagination {
        position: absolute;
        left: 50%;
        bottom: 30px;
        transform: translateX(-50%);
        color: #D7B58C;
        * {
            border-color: #D7B58C;
            color: #D7B58C;
        }
        .el-pager li {
            /*padding: 0 10px;*/
        }
        .el-pager li.active {
            color: #fff;
            background: #D7B58C;
        }
    }
</style>


