let func = {
    // 递归返回三级分类
    createClassify (arr) {
        // 判断数组是否为空
        if (Object.prototype.toString.call(arr) !== '[object Array]' || !arr.length) {
            return;
        }

        let result = [];

        arr.forEach(el => {
            result.push({
                value: el.category_id,
                label: el.c_name,
                pic: el.c_pic,
                children: func.createClassify(el.child),
            });
        });
        return result;
    },

    //根据QueryString参数名称获取值
    getQueryStringByName (name) {

        var result = location.search.match(new RegExp('[\?\&]' + name + '=([^\&]+)', 'i'));

        if (result === null || result.length < 1) {
            return '';
        }

        return result[1];

    },
};


export default func;


